<style lang="scss">
    html,body{
        margin:0;
        padding:0;
        background:#efefef;
    }
    .info-tooltip{
        font-family: Consolas,monospace;
    }
</style>
<style lang="scss" scoped>
    header {
        height: 64px;
        padding: 0 50px;
        line-height: 64px;
        background: #001529;
        .header-title {
            color: #fff;
            font-size: 20px;
        }
    }
    .main {
        max-width: 1024px;
        display: block;
        margin: 0 auto;
        margin-top: 10px;
        .box-card::v-deep .el-card__body{
            padding-left:15px;
            padding-right:15px;
        }
        .server-row {
            &.tr{
                font-family: Consolas,monospace;
            }
            flex-wrap:wrap;
            min-height: 44px;
            box-sizing: border-box;
            line-height: 44px;
            font-size: 14px;
            border-top: 1px solid #ebebeb;
            color: #595959;
            padding:0 25px;
            text-align: center;
            &::v-deep{
                .el-progress {
                    display: inline-block;
                    width: 100%;
                    .el-progress__text{
                        font-size: 14px !important;
                    }
                }
            }
            &.th {
                font-weight: bold;
                font-size: 15px;
                border: 0;
            }
            &.offline {
                background: #ffd7d7;
                color: #b00000;
            }
            .col-name {
                text-align: left;
                span{
                    position: relative;
                    sup {
                        color: #c2006f;
                        position: absolute;
                        right: -20px;
                        top: -10px;
                        display: inline-block;
                        line-height: 20px;
                    }
                }
            }
            .col-updated {
                text-align: right;
                font-family:monospace,Microsoft Yahei;
                font-size:13px;
            }
            .col-region{
                text-align: left;
                .flag-icon {
                    width: 30px
                }
            }
            .col-offline {
                text-align: center;
            }
            .col-netdev {
                padding: 2px 4px;
                text-align: center;
                div {
                    height: 20px;
                    line-height: 20px;
                    font-size: 12px;
                }
                &::v-deep{
                    .icon{
                        float: left;
                    }
                    .suffix{
                        float: right;
                    }
                }
            }
        }
    }
    footer {
        text-align: center;
        margin: 20px;
        font-size: 13px;
        color: #666;
        &::v-deep *a {
            color: #666;
        }
    }
    @media screen and (max-width:768px) {
        .server-row{
            &:nth-child(2){
                border:0
            }
            .col-name {
                font-weight:bold;
            }
            *{
                line-height:25px;
            }
            .col-region{
                text-align: center !important;
            }
            .col-netdev{
                padding: 0 !important;
                font-size:14px !important;
            }
            .up,.down{
                width:50%;
                height:25px;
                line-height:25px;
                display:inline-block;
                &::v-deep{
                    .icon,.suffix{
                        float:none !important;
                    }
                }
            }
        }
    }
</style>
<template>
        <div id="app">
        <header>
            <div class="header-title">{{info.title}}</div>
        </header>
        <main class="main">
            <el-card class="box-card">
                <el-row class="th server-row hidden-xs-only" type="flex" justify="space-around">
                    <el-col :md="3" class="col-name">名称</el-col>
                    <el-col :md="3" class="col-region">地区</el-col>
                    <el-col :md="2" class="col-uptime">运行</el-col>
                    <el-col :md="1" class="col-loadavg">负载</el-col>
                    <el-col :md="2">网络</el-col>
                    <el-col :md="3">CPU</el-col>
                    <el-col :md="3">RAM</el-col>
                    <el-col :md="3">HDD</el-col>
                    <el-col :md="1" class="col-updated">更新</el-col>
                </el-row>
                <template v-for="i in info.items">
                    <el-row :key="i.id" v-if="i.mode=='server'" :class="data[i.id].online?'online':'offline'" class="tr server-row" type="flex" justify="space-around">
                        <el-col :md="3" :xs="10" class="col-name">
                            <flag :squared="false" class="hidden-sm-and-up" :iso="i.region.toLowerCase()"/>
                            <span>
                                {{i.name}}
                                <sup>{{i.type}}</sup>
                            </span>
                        </el-col>
                        <el-col :md="3" :xs="7" class="col-region">
                            <flag class="hidden-xs-only" :squared="false" :iso="i.region.toLowerCase()"/>
                            <span>{{i.location}}</span>
                        </el-col>
                        <template v-if="data[i.id].online">
                            <el-col :md="2" :xs="4" class="col-uptime">{{s2d(data[i.id].uptime)}}</el-col>
                            <el-col :md="1" :xs="5" class="col-loadavg">
                                <el-tooltip placement="left-start">
                                    <div><span class="hidden-sm-and-up">负载</span>{{data[i.id].loadavg[0].toFixed(2)}}</div>
                                    <div slot="content" class="info-tooltip">
                                        {{data[i.id].loadavg[0].toFixed(2)}}<br>
                                        {{data[i.id].loadavg[1].toFixed(2)}}<br>
                                        {{data[i.id].loadavg[2].toFixed(2)}}
                                    </div>
                                </el-tooltip>
                            </el-col>
                            <el-col :md="2" :xs="10" class="col-netdev">
                                <div class="up" v-html="netTpl(data[i.id].net.tx,'▲')"></div>
                                <div class="down" v-html="netTpl(data[i.id].net.rx,'▼')"></div>
                            </el-col>
                            <el-col :md="1" :xs="6" class="col-updated hidden-sm-and-up">
                                <span>{{s2fr(data[i.id].last,now)}}更新</span>
                            </el-col>
                            <el-col :md="3" :xs="8">
                                <el-progress :stroke-width="12" :color="color" :percentage="Math.round(data[i.id].cpu*100)"></el-progress>
                            </el-col>
                            <el-col :md="3" :xs="8">
                                <el-tooltip placement="left-start">
                                    <el-progress :stroke-width="12" :color="color" :percentage="memperc(data[i.id])"></el-progress>
                                    <div slot="content" class="info-tooltip">
                                        Mem <br>{{fileSize(data[i.id].mem.total - data[i.id].mem.available)}}/{{fileSize(data[i.id].mem.total)}}
                                        <br>
                                        Swap<br>{{fileSize(data[i.id].mem.swap - data[i.id].mem.swapFree)}}/{{fileSize(data[i.id].mem.swap)}}
                                    </div>
                                </el-tooltip>
                            </el-col>
                            <el-col :md="3" :xs="8">
                                <el-tooltip placement="left-start">
                                    <el-progress :stroke-width="12" :color="color" :percentage="hddperc(data[i.id])"></el-progress>
                                    <div slot="content" class="info-tooltip">
                                        {{fileSize(data[i.id].disk.used)}}/{{fileSize(data[i.id].disk.total)}}
                                    </div>
                                </el-tooltip>
                            </el-col>
                        </template>
                        <template v-else>
                            <el-col :md="1" :xs="4" class="col-updated hidden-sm-and-up">
                                <span>{{s2fr(data[i.id].last,now)}}</span>
                            </el-col>
                            <el-col :md="16" :xs="10" class="col-offline">离线</el-col>
                        </template>
                        <el-col :md="1" :xs="3" class="col-updated hidden-xs-only">{{s2fr(data[i.id].last,now)}}</el-col>
                    </el-row> 
                </template>
            </el-card>
        </main>
        <footer>
            <div v-html="config.footer"></div>
        </footer>
    </div>
</template>

<script>
const config = window._TOKI_PRE_CONFIG;
import fly from 'flyio';
export default {
    name: 'App',
    data(){
        return {
            now:0,
            info:{
                title:"ServerStatus",
                items:[]
            },
            data:{},
            config:config
        }
    },
    methods:{
        netTpl(fileSize,a) {
            let n,w;
            if (fileSize < 1) {
                n = (fileSize*1024).toFixed(2);
                w = "K";
            } else if (fileSize < 1024) {
                n = fileSize.toFixed(2)
                w = 'M';
            } else if (fileSize < 1024*1024) {
                var temp = fileSize / (1024);
                n = temp.toFixed(2);
                w = 'G';
            } else {
                var temp = fileSize / (1024*1024);
                n = temp.toFixed(2);
                w = 'T';
            }
            return `<span class="icon">${a}</span><span class="n">${n}</span><span class="suffix">${w}iB</span>`;
        },
        fileSize(fileSize) {
            let n,w;
            if (fileSize < 1) {
                n = (fileSize*1024).toFixed(2);
                w = "K";
            } else if (fileSize < 1024) {
                n = fileSize.toFixed(2)
                w = 'M';
            } else if (fileSize < 1024*1024) {
                var temp = fileSize / (1024);
                n = temp.toFixed(2);
                w = 'G';
            } else {
                var temp = fileSize / (1024*1024);
                n = temp.toFixed(2);
                w = 'T';
            }
            return `${n} ${w}iB`;
        },
        memperc(d){
            return Math.round((d.mem.total-d.mem.available)/d.mem.total*100)
        },
        hddperc(d){
            return Math.round(d.disk.used/d.disk.total*100)
        },
        color(p){
            if(p>85)return "#F56C6C";
            if(p>70)return "#E6A23C";
            return "#409EFF";
        },
        s2d(s) { 
            let day = Math.floor( s/ (24*3600) ); // Math.floor()向下取整 
            let hour = Math.floor( (s - day*24*3600) / 3600); 
            let minute = Math.floor( (s - day*24*3600 - hour*3600) /60 ); 
            let second = s - day*24*3600 - hour*3600 - minute*60; 
            return day>0?(day + "天"):hour+"小时"; 
        },
        s2fr(s,n){
            n = n||Date.now();
            s = Math.round((n-s)/1000)
            if(s<3)return "刚刚";
            let day = Math.floor( s/ (24*3600) ); // Math.floor()向下取整 
            let hour = Math.floor( (s - day*24*3600) / 3600); 
            let minute = Math.floor( (s - day*24*3600 - hour*3600) /60 ); 
            let second = s - day*24*3600 - hour*3600 - minute*60;
            return  day>0?("很久前"):
                    hour>0?hour+"时前":
                    minute>0?minute+"分前":
                    second+"秒前"; 
        },
        show(){
            document.getElementById("loader").style.display="none"
            document.getElementById("app").style.display="block"
        }
    },
    async created(){
        setInterval(function(){
            this.now = Date.now();
        }.bind(this),1000);
        this.$watch("info",function(v){
            document.title = this.info.title;
        })
        if(config.interval<=0){
            let io = (await import("socket.io-client")).default;
            let socket = io(config.server+"/public",{transports: ['websocket']});
            this.$socket = socket;
            socket.on('reconnect_attempt', () => {
                socket.io.opts.transports = ['polling', 'websocket'];
            });
            socket.emit("subscribe",config.namespace);
            socket.on('info', (e) => {
                this.info = e;
            });
            socket.on('first', (e) => {
                this.data = e;
                this.show()
            });
            socket.on('update', (e) => {
                this.data[e[0]] = e[1];
            });
        }else{
            let nsp = fly.get(config.server+"/namespace/"+config.namespace);
            let box = fly.get(config.server+"/box/"+config.namespace);
            this.info = (await nsp).data.data;
            this.data = (await box).data.data;
            this.show()
            setInterval(async function(){
                let box = fly.get(config.server+"/box/"+config.namespace);
                this.data = (await box).data.data;
            }.bind(this),config.interval)
        }
    }
}
</script>